export class CanvasWorker {
  constructor(draw, active=true) {
    this.draw = draw;
    this.active = false;
    this._loop = (t)=>this.animation(t);
    this.setState(active);
  }

  play() {
    if (this.active) return;
    this.active = true;
    window.requestAnimationFrame(this._loop);
  }

  stop() {this.active = false;}

  setState(active) {
    if (!active) this.stop();
    else if (!this.active) this.play();
  }

  animation(time) {
    if (!this.active) return;
    this.draw(time);
    window.requestAnimationFrame(this._loop);
  }
}