import {
  newPerspective,
  applyPerspective,
  binSearch,
  applyScreen,
  rgb, rgba, clamp, mix, mix3, rnd2dCircle, rnd2d
} from "./Utils.js";
import { DomController, STATES } from "./DomController.js";
import { DrawObject, DrawLine, DrawPoint, DrawTrio, DrawPatentCard, DrawPatentYear, DrawEndCard } from "./DrawObjects.js";
import Srand from "seeded-rand";
import { SETTINGS as S } from "./GlobalSettings.js";
import PATENT_TREES from "../../content/patent-trees.json"

const SQRT3 = Math.sqrt(3);


export class Stats {
  constructor () {
    this.FPS = 0.0;
    this.dt = 0.0;
    this.ft = 0.0;
    this.lastTime = 0.0;
    this.drawObjects = 0;
    this.genPoints = 0;
    this.genLines = 0;
  }
}

export class MainDemo {
  constructor (canvas, controller) {
    this.canvas = canvas;
    this.controller = controller;
    this.context = canvas.getContext('2d', {alpha: false, desynchronized: true});
    this.FOV = S.CAM_FOV / 180.0 * Math.PI;
    this.ZNEAR = S.CAM_ZNEAR;
    this.ZFAR = S.CAM_ZFAR;
    this.stats = new Stats();
    this.srand = new Srand();
    this.lastScrolledTime = 0;

    window.addEventListener("resize", ()=>this.resized());
    //window.addEventListener("scroll", ()=>this.scrolled());
    this.resized();
    this.generateDemo();
  }
  getAspect() {
    return this.canvas.width / this.canvas.height;
  }
  resized() {
    this.canvas.height = this.canvas.clientHeight;
    this.canvas.width = this.canvas.clientWidth;
    // cache
    this.windowInnerHeight = window.innerHeight;
    this.windowInnerWidth = window.innerWidth;
    this.changeFOV();
  }
  changeFOV() {
    this.perspective = newPerspective(this.FOV, this.getAspect(), this.ZNEAR, this.ZFAR);
    this.controller.updateS(this);
  }
  scrolled() {
    if (this.stats.lastTime - this.lastScrolledTime > 100) this.controller.enableMouseEvents();

    let procent = window.pageYOffset / (S.GEN_HEIGHT - this.windowInnerHeight);
    if (this.procent == procent) return;
    if (this.procent === undefined) this.procent = procent;

    this.culling(procent);
  }

  smoothScroll(target) {
    const SCROLL_SPEED = .15 * Math.abs(target - this.procent);
    if (SCROLL_SPEED < 0.0001) this.procent = target;
    const delta = (target - this.procent);
    this.procent += (delta > 0 ? Math.min(delta, SCROLL_SPEED) : Math.max(delta, -SCROLL_SPEED));
  }
  culling(procent=this.procent) {
    this.smoothScroll(procent);

    this.currentZ = this.procent * this.objects[this.objects.length-1].z;
    this.firstId = binSearch(this.objects, val=>(val.z - this.currentZ));
    this.lastId = this.firstId;
    for (;this.lastId < this.objects.length && this.objects[this.lastId].z < this.currentZ + this.ZFAR; this.lastId++);

    this.stats.drawObjects = this.lastId - this.firstId;
    this.lastScrolledTime = this.stats.lastTime;
    this.controller.hidePatents();
    this.controller.disableMouseEvents();
  }

  findScroll(z) {
    return (z / this.objects[this.objects.length-1].z) * (S.GEN_HEIGHT - this.windowInnerHeight);
  }

  generateDemo(tree="1") {
    this.tree = tree;
    this.srand.seed(this.srand.seed());
    this.controller.setScrollHeight(S.GEN_HEIGHT-S.GEN_HEIGHT_LIM);
    this.controller.clearPatents();
    this.objects = [];
    const P_COUNT = S.GEN_COUNT;
    this.objects = new Array(P_COUNT);

    const CUR_PATENT_TREE = PATENT_TREES[tree];
    const PATENTS = CUR_PATENT_TREE.length+1;
    const SPEC_DIST = Math.floor(P_COUNT/PATENTS);
    let isSpecial = (i)=>(i>0 && i%SPEC_DIST==0);
    this.specials = [];
    this.specialId = 0;

    const Z_K = S.GEN_HEIGHT / P_COUNT;
    let lastX = 0.0;
    for (let i = 0; i < P_COUNT; i++)
    {
      let x,y; let z = i * S.GEN_ZSCALE;
      if (S.GEN_CIRCLE) [x,y] = rnd2dCircle(this.srand);
      else [x,y] = rnd2d(this.srand);

      const p3 = [0,0,z];
      if (isSpecial(i)) {
        p3[0] = (x>0 ? S.GEN_SPEC_MX : -S.GEN_SPEC_MX) + x*S.GEN_SPEC_RX;
        p3[1] = (y>0 ? S.GEN_SPEC_MY : -S.GEN_SPEC_MY) + y*S.GEN_SPEC_RY;
        p3[0] = lastX > 0 ? Math.abs(p3[0]) : -Math.abs(p3[0]);
        lastX = !lastX;
      } else {
        [p3[0],p3[1]] = [x*S.GEN_RADIUS,y*S.GEN_RADIUS];
      }

      if (isSpecial(i)) {
        if (this.specials.length == PATENTS - 1) [p3[0],p3[1]] = [0,0];
        this.specials.push(p3);
        let specId = Math.min(PATENTS, this.specials.length)-1;

        const p3n = [p3[0],p3[1]];
        p3n[0] = ((p3n[0] > 0 ? -S.PAT_TEXT_OX : S.PAT_TEXT_OX))*250;
        p3n[1] = ((p3n[1] > 0 ? S.PAT_TEXT_OY : -S.PAT_TEXT_OY))*250;

        if (specId < PATENTS-1) {
          const CUR_PATENT = CUR_PATENT_TREE[specId];
          const year = CUR_PATENT.issue_date.split(".").pop();
          const title_img = CUR_PATENT.label;

          const img_root = title_img.split("-").shift();
          this.objects[i] = new DrawPatentCard(p3, this, `./img/patents/tree${img_root}-dark/${title_img}`, year, CUR_PATENT.name, p3n, CUR_PATENT);
        } else {
          const ENDINGS = [" ", "  ", "   ", "  ", "  "];
          this.objects[i] = new DrawEndCard(p3, this, this.controller.endCard, ENDINGS[0], 0);
          const p3n = [p3[0], p3[1], p3[2]+.1];
          const CLICKS = [undefined, ()=>{this.controller.gotoObjectsEvent()}, ()=>this.controller.nextTree(), ()=>{this.controller.gotoGameEvent()}, ()=>{this.controller.gotoVideoEvent()}];
          for (let i = 1; i < 5; i++) {
            this.objects.push(new DrawEndCard(p3n, this, this.controller.endCardSelection, ENDINGS[i], i, CLICKS[i]));
          }
        }
      }
      else this.objects[i] = new DrawPoint(p3);
    }
    const MAX_DEGREE = S.GEN_DEGREE;
    let isUsed = new Array(P_COUNT);
    for (let y = 0; y < P_COUNT; y++) {
      let x;
      for (x = y + 1; x < P_COUNT && x < y + 1 + MAX_DEGREE; x++) {
        const spec = isSpecial(x) || isSpecial(y);
        let skip = false;
        if (!spec && this.srand.random() < S.GEN_SKIP_R) skip = true;
        const p1 = this.objects[y].p3;
        const p2 = this.objects[x].p3;
        const pd = [p1[0]-p2[0],p1[1]-p2[1]]; // point difference
        const cd = Math.abs(p1[0] * pd[1] - p1[1] * pd[0]); // central diameter
        // TODO: Chooze medium Z between p1 and p2
        // Make projection, then find CD

        if (!spec && this.srand.random() < S.GEN_TUNNEL/cd) skip = true;
        //if (spec) { p1[2] -= .1; p2[2] -= .1; }

        if (skip) continue;
        isUsed[x] = true; isUsed[y] = true;
        this.objects.push(new DrawLine(p1, p2, spec ? [...S.ACNT_COLOR,1.0] : S.LINE_COLOR));
      }
      for (; x < P_COUNT && x < y + 1 + 40; x++) {
        const spec = isSpecial(x) || isSpecial(y);
        if (!spec) { this.srand.random(); this.srand.random(); }
      }
    }

    //this.objects = this.objects.filter((_obj,i)=>i>=P_COUNT||isUsed[i]);
    this.objects.sort((a,b)=>a.z-b.z);

    this.stats.genPoints = this.objects.reduce((s,val)=>{return s + !!(val instanceof DrawPoint);}, 0);
    this.stats.genLines = this.objects.reduce((s,val)=>{return s + !!(val instanceof DrawLine);}, 0);
  }

  getRDepth(obj) {
    const cz = obj.z - this.currentZ;
    let d = cz / this.ZFAR; //1 - far; 0 - near
    d = 1 - d; d = 1 - d*d; // square
    return d;
  }
  getDepth(obj) {
    const cz = obj.z - this.currentZ;
    if (cz > this.ZNEAR) {
      return this.getRDepth(obj);
    } else return (this.ZNEAR-cz)/this.ZNEAR;
  }
  getWave(obj, time) {
    if (!S.WAVE_ACTIVE) return 0.0;
    const kpd = (S.WAVE_DURATION + S.WAVE_DELAY)*1000.0;
    const p = S.WAVE_DELAY*1000.0/kpd;
    // sin(max(0, mod(x/(k+d),1)-d/(k+d))/(1-d/(k+d))*pi), just a sine wave with delay

    let wave = Math.max(0, Math.abs((S.WAVE_SCALE*obj.z+time/kpd))%1-p)/(1-p);
    wave = Math.sin(wave * Math.PI);
    return wave;
  }

  draw (time) {
    const t0 = performance.now();
    this.stats.dt = time - this.stats.lastTime;
    this.stats.FPS = 1000.0/this.stats.dt;

    this.context.fillStyle = rgb(...S.BACK_COLOR);
    this.context.fillRect(0, 0, this.canvas.width, this.canvas.height);

    if (this.controller.state == STATES.CHANGING) {
      let selectedTree = `${this.controller.selectedTree+1}`;

      let targetScroll = -this.ZFAR / this.objects[this.objects.length-1].z;

      if(this.procent <= targetScroll && this.tree != selectedTree) {
        this.generateDemo(selectedTree);
        window.scrollTo(0,0);
        this.procent = -this.ZFAR / this.objects[this.objects.length-1].z;
        this.controller.changeState(STATES.INITIAL);
        this.culling();
      } else this.culling(targetScroll);

    } else this.scrolled();


    if (this.controller.state != STATES.PATENT)
      for (let i = this.lastId-1; i >= this.firstId; i--) {
        this.objects[i].draw(this, time);
      }

    const t1 = performance.now();
    this.stats.ft = t1-t0;
    this.stats.lastTime = time;
  }
}