import {
  applyPerspective,
  applyScreen,
  rgb, rgba, clamp, mix, mix3
} from "./Utils.js";
import { SETTINGS as S } from "./GlobalSettings.js";
import { STATES } from "./DomController.js";
import BezierEasing from "bezier-easing";

const BEZIER1 = BezierEasing(.5,0,.5,1);

export class DrawObject {
  constructor (z) {
    this.z = z;
  }
  draw (mainDemo, time) { }
}

export class DrawPoint extends DrawObject {
  constructor (p3) {
    super (p3[2]);
    this.p3 = p3;
  }
  draw (mainDemo, time) {
    const d = mainDemo.getDepth(this);
    const w = mainDemo.getWave(this, time);
    const RADIUS = S.DOTS_RADIUS*(1+S.WAVE_RADIUS*w);
    let p3 = [...this.p3]; p3[2] -= mainDemo.currentZ;
    // let rr = Math.sin(p3[2]*12345.6789); //rr -= Math.floor(rr);
    // p3[1] += Math.sin(rr+time*.001)*.01; 

    let p3s = [p3[0], p3[1]+RADIUS, p3[2]];
    p3 = applyPerspective(mainDemo.perspective, p3);
    p3s = applyPerspective(mainDemo.perspective, p3s);
    const scale = Math.abs(p3s[1] - p3[1]);
    const p2d = applyScreen(p3, mainDemo.canvas.width, mainDemo.canvas.height);

    let col = mix3(...S.DOTS_COLOR, ...S.WAVE_COLOR.slice(0,3), w*S.WAVE_COLOR[3]);
    col = mix3(...col, ...S.BACK_COLOR, clamp(d,0,1));
    // let stcol = mix3(...S.STRO_COLOR, ...S.BACK_COLOR, clamp(d,0,1));
    mainDemo.context.fillStyle = rgb(...col);
    // mainDemo.context.strokeStyle = rgb(...stcol); 
    mainDemo.context.lineWidth = S.DOTS_STROKE*scale;
    mainDemo.context.beginPath();
    mainDemo.context.arc(p2d[0], p2d[1], scale, 0, 2 * Math.PI, false);
    mainDemo.context.fill();
    if (S.DOTS_STROKE > 0) mainDemo.context.stroke();
    mainDemo.context.closePath();
  }
}

export class DrawLine extends DrawObject {
  constructor (p31, p32, col=S.LINE_COLOR) {
    super (Math.min(p31[2], p32[2]));
    this.p31 = p31;
    this.p32 = p32;
    this.col = col;
  }
  draw (mainDemo, time) {
    let p31 = [...this.p31]; p31[2] -= mainDemo.currentZ;
    let p32 = [...this.p32]; p32[2] -= mainDemo.currentZ;
    p31[2] = Math.max(p31[2], 0);
    p32[2] = Math.max(p32[2], 0);
    p31 = applyPerspective(mainDemo.perspective, p31);
    p32 = applyPerspective(mainDemo.perspective, p32);

    const p2d1 = applyScreen(p31, mainDemo.canvas.width, mainDemo.canvas.height);
    const p2d2 = applyScreen(p32, mainDemo.canvas.width, mainDemo.canvas.height);

    let d = mainDemo.getDepth(this);
    let w = mainDemo.getWave(this, time);

    let col = mix3(...this.col.slice(0,3), ...S.WAVE_COLOR.slice(0,3), w*S.WAVE_COLOR[3]);
    mainDemo.context.strokeStyle = rgba(...col, (1-d)*this.col[3]);
    mainDemo.context.lineWidth = S.LINE_THICK*(1+w*S.WAVE_THICK);
    mainDemo.context.beginPath();
    mainDemo.context.moveTo(p2d1[0], p2d1[1]);
    mainDemo.context.lineTo(p2d2[0], p2d2[1]);
    mainDemo.context.stroke();
    mainDemo.context.closePath();
    mainDemo.context.strokeStyle = '';
  }
}

export class DrawTrio extends DrawObject {
  // static TRI_R = .3;
  constructor (p3) {
    super(p3[2]);
    const c6 = Math.cos(Math.PI*2/3); const s6 = Math.sin(Math.PI*2/3);
    this.p3 = p3;
    this.p31 = [p3[0],p3[1]+DrawTrio.TRI_R,p3[2]];
    this.p32 = [p3[0]+s6*DrawTrio.TRI_R,p3[1]+c6*DrawTrio.TRI_R,p3[2]];
    this.p33 = [p3[0]-s6*DrawTrio.TRI_R,p3[1]+c6*DrawTrio.TRI_R,p3[2]];
  }
  draw (mainDemo, time) {
    let p31 = [...this.p31]; p31[2] -= mainDemo.currentZ;
    let p32 = [...this.p32]; p32[2] -= mainDemo.currentZ;
    let p33 = [...this.p33]; p33[2] -= mainDemo.currentZ;
    let d = mainDemo.getDepth(this);
    d = clamp(d*1.3-.3,0,1);
    mainDemo.context.fillStyle = rgba(...S.ACNT_COLOR,1-d);
    DrawTrio.drawTriangle(p31,p32,p33,mainDemo.canvas,mainDemo.context,mainDemo.perspective);
    mainDemo.context.fill();
  }
  static drawTriangle(p31,p32,p33,canvas,context,perspective)
  {
    p31 = applyPerspective(perspective, p31);
    p32 = applyPerspective(perspective, p32);
    p33 = applyPerspective(perspective, p33);
    const p2d1 = applyScreen(p31, canvas.width, canvas.height);
    const p2d2 = applyScreen(p32, canvas.width, canvas.height);
    const p2d3 = applyScreen(p33, canvas.width, canvas.height);
    context.beginPath();
    context.moveTo(p2d1[0], p2d1[1]);
    context.lineTo(p2d2[0], p2d2[1]);
    context.lineTo(p2d3[0], p2d3[1]);
    context.closePath();
  }
}

// ---- ELEMENT BASED RENDERING ----

export class DrawHTMLObject extends DrawObject {
  constructor (p3, controller, template) {
    super (p3[2]);
    this.p3 = p3;
    const newPatent = template.content.cloneNode(true);

    controller.patents.appendChild(newPatent);
    this.element = controller.patents.lastElementChild;
    this.controller = controller;
    this.element.style.zIndex = `${p3[2]}`;
    this.rot = 0.0;
  }

  draw (mainDemo, time) {
    this.calcStyle (mainDemo, time);
    this.applyStyle ();
  }

  calcStyle (mainDemo, time) {
    let p3 = [...this.p3]; p3[2] -= mainDemo.currentZ;

    // let p3p = applyPerspective(mainDemo.perspective, p3);
    const p2 = [-p3[0]*this.controller.perspective, -p3[1]*this.controller.perspective];

    let z = p3[2] - 1;
    let d = mainDemo.getRDepth(this);
    let od = clamp((d-0.8)*5.,0,1); // (d-.8)*5.

    this.opacity = 1-od;
    this.tr3d = [p2[0],p2[1], -this.controller.perspective*z];
    this.scale = S.PAT_SCALE;
  }

  applyStyle () {
    this.element.style.opacity = this.opacity;
    this.element.style.transform = `translate(-50%,-50%) translate3d(${this.tr3d[0]}px,${this.tr3d[1]}px,${this.tr3d[2]}px) rotateY(${this.rot}deg) scale(${this.scale})`;
  }
}

export class DrawPatentCard extends DrawHTMLObject {
  constructor (p3, mainDemo, img, year, description, yp2, patent) {
    super(p3, mainDemo.controller, mainDemo.controller.patentCard);
    this.element.querySelector(".patent-image img").src = img;
    this.element.addEventListener("click", ()=>this.click(mainDemo, patent));
    this.opened = false;
    this.element.style.zIndex = `${Math.floor(1000.0-this.z)}`;
    this.yearEl = this.element.querySelector(".patent-year");
    this.yearEl.innerText = year;
    this.yearEl.style.transform = `translate(-50%,-50%) translate(${yp2[0]}px,${yp2[1]}px)`;
    this.descEl = this.element.querySelector(".patent-description");
    this.descEl.innerText = description;
  }
  draw (mainDemo, time) {
    if (this.controller.state == STATES.PATENT) return;
    super.calcStyle (mainDemo, time);
    this.rot = Math.sign(this.p3[0])*S.PAT_ROTATE*(clamp(1-1/0.5*mainDemo.getRDepth(this),0,1));

    if (this.opened && this.controller.state == STATES.OPENNING) {
      let pr = (time - this.controller.stateTime) * 0.001; // 1s
      pr = Math.min(1.0, pr);
      pr = BEZIER1(pr);
      this.yearEl.style.opacity = mix(1, 0, pr);

      this.tr3d[0] = mix(this.tr3d[0], 0, pr);
      this.tr3d[1] = mix(this.tr3d[1], 0, pr);
      this.rot = mix(this.rot, 0, pr);

      let pr2 = (time - this.controller.stateTime - 1000) * 0.002; // 500ms
      pr2 = Math.min(1.0, pr2);
      if (pr2 > 0) this.scale += Math.sin(BEZIER1(pr2)*Math.PI)*.2;

      if (pr2 >= 1) this.controller.openPopupCircle();
      let pr3 = (time - this.controller.stateTime - 1800) * 0.004; // 250ms
      pr3 = Math.min(1.0, pr3);
      if (pr3 > 0) this.opacity = mix(this.opacity, 0, pr3);

      if (this.opened && this.controller.state == STATES.OPENNING &&
          time - this.controller.stateTime - 2500 > 0)
        this.controller.changeState(STATES.PATENT, time);
    } else if (this.controller.state == STATES.OPENNING) {
      let pr = (time - this.controller.stateTime) * 0.001; // 1s
      pr = Math.min(1.0, pr);
      this.opacity = mix(this.opacity, 0, pr);
    } else if (this.opened && this.controller.state == STATES.CLOSING) {
      let pr3 = (time - this.controller.stateTime) * 0.004; // 250ms
      this.opacity = mix(0, this.opacity, Math.min(1.0, pr3));

      if (pr3 >= 2.0) this.controller.closePopupCircle(); // 500ms
      let pr2 = (time - this.controller.stateTime - 1000) * 0.001; // 1000ms
      pr2 = clamp(pr2, 0, 1);
      pr2 = BEZIER1(pr2);
      this.yearEl.style.opacity = mix(0, 1, pr2);
      this.tr3d[0] = mix(0, this.tr3d[0], pr2);
      this.tr3d[1] = mix(0, this.tr3d[1], pr2);
      this.rot = mix(0, this.rot, pr2);

      if (time - this.controller.stateTime - 2000 > 0) {
        this.controller.changeState(STATES.INITIAL, time);
        this.opened = false;
        this.yearEl.style.opacity = 1;
        this.element.parentNode.style.zIndex = "";
      }
    } else if (this.controller.state == STATES.CLOSING) {
      let pr = (time - this.controller.stateTime - 1000) * 0.001; // 1s
      pr = clamp(pr, 0, 1);
      this.opacity = mix(0, this.opacity, pr);
    }

    if (this.controller.state != STATES.PATENT)
      super.applyStyle ();
    if (this.controller.state == STATES.INITIAL)
      this.element.style.pointerEvents = "";
  }
  click (mainDemo, patent) {
    if (this.controller.state == STATES.INITIAL) {
      this.controller.loadPatent(patent);
      window.scrollTo(0, mainDemo.findScroll(this.z - 6));
      this.controller.changeState(STATES.OPENNING);
      this.opened = true;
      this.element.parentNode.style.zIndex = 18;
    }
  }
}

export class DrawPatentYear extends DrawHTMLObject {
  constructor (p3, controller, text) {
    super(p3, controller, controller.patentYear);
    this.element.querySelector(".text").innerText = text;
    this.element.style.zIndex = `${Math.floor(1000.0-this.z)}`;
  }
  draw (mainDemo, time) {
    if (this.controller.state == STATES.PATENT) return;
    super.calcStyle (mainDemo, time);

    if (this.controller.state == STATES.OPENNING){
      let pr = (time - this.controller.stateTime) * 0.001; // 1s
      pr = Math.min(1.0, pr);
      this.opacity = mix(this.opacity, 0, pr);
    } else if (this.controller.state == STATES.CLOSING){
      let pr = (time - this.controller.stateTime - 1000) * 0.001; // 1s
      pr = clamp(pr, 0, 1);
      this.opacity = mix(0, this.opacity, pr);
    }

    super.applyStyle ();
  }
}

export class DrawEndCard extends DrawHTMLObject {
  constructor (p3, mainDemo, template, text, id, click=undefined) {
    super([...p3], mainDemo.controller, template);
    this.id = id;
    let textEl = this.element.querySelector(".patent-description");
    textEl.innerText = text;
    this.click = click;
    if (click)
      this.element.addEventListener("click", ()=>this.click(mainDemo, this));

    if (id != 0) {
      const elHeight = this.element.clientHeight;
      let padding = ((elHeight - textEl.getBoundingClientRect().height) * .5)/mainDemo.canvas.height*100.0;
      for(let i = 0; i < 8; i++) {
        textEl.style.marginTop = padding + "vh";
        let newPadding = ((elHeight - textEl.getBoundingClientRect().height) * .5)/mainDemo.canvas.height*100.0;
        padding = (padding + newPadding) * .5;
      }
    }

  }

  draw (mainDemo, time) {
    let isMobile = false;
    if (this.id > 0) {
      const r = 1.9;
      let circleId = this.id;

      isMobile = (mainDemo.canvas.width / mainDemo.canvas.height < .85);
      if (isMobile)
        circleId = circleId - (circleId == 1) + (circleId == 4);

      this.p3[0] = Math.cos(Math.PI/3 * (circleId-1))*r;
      this.p3[1] = Math.sin(Math.PI/3 * (circleId-1))*r;
    }
    super.calcStyle (mainDemo, time);
    //if (isMobile)
    //  this.scale *= .8;

    if (this.controller.state == STATES.OPENNING){
      let pr = (time - this.controller.stateTime) * 0.001; // 1s
      pr = Math.min(1.0, pr);
      this.opacity = mix(this.opacity, 0, pr);
    } else if (this.controller.state == STATES.CLOSING){
      let pr = (time - this.controller.stateTime - 1000) * 0.001; // 1s
      pr = clamp(pr, 0, 1);
      this.opacity = mix(0, this.opacity, pr);
    }

    if (this.click && this.controller.state == STATES.INITIAL)
      this.element.style.pointerEvents = "";

    const maxP = (S.GEN_HEIGHT - mainDemo.windowInnerHeight - S.GEN_HEIGHT_LIM) / (S.GEN_HEIGHT - mainDemo.windowInnerHeight);
    const maxZ = maxP * mainDemo.objects[mainDemo.objects.length-1].z;
    const procent = BEZIER1(clamp(1 - (maxZ - mainDemo.currentZ)*.1 + (4-this.id) * .1, 0, 1));

    this.tr3d[0] = mix(0, this.tr3d[0], procent);
    this.tr3d[1] = mix(0, this.tr3d[1], procent);
    if (this.id != 0)
      this.scale = mix(0.3, this.scale, procent);
    super.applyStyle ();
  }
}