import { MainDemo } from "./Demo/MainDemo.js";
import { SETTINGS } from "./Demo/GlobalSettings.js";
import { CanvasWorker } from "./Demo/CanvasWorker.js";
import { DomController } from "./Demo/DomController.js";
import { download, upload } from "./Demo/Utils.js";

let canvas = document.getElementById("flight-canvas");
let patent = document.getElementById("flight-patent-board");
let controller = new DomController(canvas, patent);
let demo = new MainDemo(canvas, controller);
let worker = new CanvasWorker((t)=>demo.draw(t));
worker.play();

window._flightController = controller;
window._flightWorker = worker;
