import { applyPerspective, applyScreen } from "./Utils.js";
import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock";
import Swiper, { Navigation, Thumbs, Zoom } from 'swiper';
import ScrollBooster from "scrollbooster";

// configure Swiper to use modules
Swiper.use([Navigation, Thumbs, Zoom]);

const PATENTS_DIV = window.document.getElementById("flight-elements-container");
const PATENT_TMPL = window.document.getElementById("flight-el-patent-card-tmpl");
const PATENT_Y_TMPL = window.document.getElementById("flight-el-patent-year-tmpl");
const END_CARD_TMPL = window.document.getElementById("flight-el-end-card-tmpl");
const END_CARD_SELECT_TMPL = window.document.getElementById("flight-el-end-card-select-tmpl");
const POPUP_CIRCLE = window.document.getElementById("flight-pop-circle");
const FLIGHT_TREE_SELECTOR = window.document.getElementById("flight-btn-trees");

export const STATES = {
  INITIAL:0,
  OPENNING:2,
  PATENT:3,
  CLOSING:5,
  CHANGING:6
}

export class DomController {
  constructor (canvas, patent) {
    this.canvas = canvas;
    this.patent = patent;
    this.patent.querySelector(".close-btn").addEventListener("click", ()=>this.onPatentCloseClick());
    this.patents = PATENTS_DIV;
    this.patentCard = PATENT_TMPL;
    this.patentYear = PATENT_Y_TMPL;
    this.endCard = END_CARD_TMPL;
    this.endCardSelection = END_CARD_SELECT_TMPL;
    this.popupCircle = POPUP_CIRCLE;
    this.treeSelector = FLIGHT_TREE_SELECTOR;
    this.treeSelectorBtns = this.treeSelector.querySelectorAll(".patent-tree-btn");
    this.selectedTree = 0;
    this.state = STATES.INITIAL;
    this.stateTime = -1000;

    this.patentComponents = {};
    this.patentComponents.header = this.patent.querySelector(".container .header h1");
    this.patentComponents.category = this.patent.querySelector(".container .category span");
    this.patentComponents.description = this.patent.querySelector(".container .description span");
    this.patentComponents.slides = this.patent.querySelector(".container .patent-image .swiper-wrapper");
    this.patentComponents.thumbs = this.patent.querySelector(".container .patents-selection .swiper-wrapper");
    this.patentComponents.props = this.patent.querySelectorAll(".container .props td");
    this.patentComponents.link = this.patent.querySelector(".container .props td a");

    this.patentComponents.thumbsContainer = this.patent.querySelector(".container .patents-selection .swiper-container");
    this.patentComponents.slidesContainer = this.patent.querySelector(".container .patent-image .swiper-container");

    this.patentComponents.galleryThumbs = new Swiper(this.patentComponents.thumbsContainer, {
      freeMode: true,
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
      slidesPerView: 'auto',
      centerInsufficientSlides: true,
    });

    this.patentComponents.gallerySlides = new Swiper(this.patentComponents.slidesContainer, {
      zoom: true,
      slidesPerView: 1,
      thumbs: {
        swiper: this.patentComponents.galleryThumbs,
      },
      navigation: {
        nextEl: '#btn__swiper-next',
        prevEl: '#btn__swiper-prev',
  },
      threshold: 2,
    });


    for(let i = 0; i < this.treeSelectorBtns.length; i++)
      this.treeSelectorBtns[i].addEventListener("click", ()=>{this.selectTree(i);});

    this.selectTree(this.selectedTree);
    initializeScrollBooster();

    this.gotoObjectsEvent = ()=>{};
    this.gotoGameEvent = ()=>{};
    this.gotoVideoEvent = ()=>{};
  }

  setScrollHeight(height) {
    window.document.body.style.height = height + "px";
  }
  showPatent() {
    this.patent.style.visibility = "";
    this.patents.style.visibility = "hidden";
  }
  hidePatent() {
    this.patent.style.visibility = "hidden";
    this.patents.style.visibility = "";
  }

  onPatentCloseClick(){
    this.openPopupCircle();
    setTimeout(()=>{
      this.changeState(STATES.CLOSING);
      this.hidePatent();
    }, 500);
  }

  hidePatents(){
    for (let v = this.patents.firstElementChild; v !== null; v = v.nextElementSibling) {
      v.style.opacity = "0";
      v.style.pointerEvents = "none";
    }
  }
  changeState(state, time=performance.now()){
    this.state = state;
    this.stateTime = time;

    if (this.state == STATES.PATENT) {
      this.hidePatents();
      this.showPatent();
      this.closePopupCircle();
    }
    if (this.state == STATES.INITIAL)
      enableBodyScroll(this.patent);
    else disableBodyScroll(this.patent);
  }
  clearPatents(){
    this.patents.innerHTML = "";
  }
  disableMouseEvents(){
    this.patents.style.pointerEvents = "none";
  }
  enableMouseEvents(){
    this.patents.style.pointerEvents = "";
  }

  openPopupCircle(){
    this.popupCircle.classList.add("opened");
  }
  closePopupCircle(){
    this.popupCircle.classList.remove("opened");
  }

  updateS(mainDemo){
    this.perspective = (this.canvas.clientHeight*.5/Math.tan(mainDemo.FOV*.5));
    this.patents.style.perspective = this.perspective+"px";
  }

  loadPatent(patent){
    this.patentComponents.header.innerText = patent["name"];
    this.patentComponents.category.innerText = patent["category"];
    this.patentComponents.description.innerText = patent["text"];

    let props = ["number","issue_date","application_date","department","applicants","inventors"];

    for (let i = 0; i < props.length; i++)
      this.patentComponents.props[i].innerText = patent[props[i]];
    this.patentComponents.link.href = patent["link"];

    this.patentComponents.slides.innerHTML = this.patentComponents.thumbs.innerHTML = "";
    for (let i = 0; i < patent["imgs"].length; i++) {
      let img = document.createElement("div");

      const img_name = patent["imgs"][i];
      const img_root = img_name.split('-').shift();
      let imgUrl = `./img/patents/tree${img_root}-light/${img_name}`;
      img.classList.add("swiper-slide");
      img.style.backgroundImage=`url(${imgUrl})`;

      this.patentComponents.thumbs.appendChild(img);

      img = document.createElement("img"); img.src = imgUrl;
      let container = document.createElement("div");
      container.classList.add("swiper-slide");
      let container2 = document.createElement("div");
      container2.classList.add("swiper-zoom-container");
      container2.appendChild(img);
      container.appendChild(container2);
      this.patentComponents.slides.appendChild(container);
    }

    this.patentComponents.galleryThumbs.update();
    this.patentComponents.gallerySlides.update();
  }

  selectTree(id){
    for(let i = 0; i < this.treeSelectorBtns.length; i++)
      this.treeSelectorBtns[i].classList.remove("selected");
    this.treeSelectorBtns[id].classList.add("selected");

    if (id == this.selectedTree || id < 0 || id >= this.treeSelectorBtns.length)
      return;
    this.selectedTree = id;

    this.changeState(STATES.CHANGING);
  }
  nextTree(){
    this.selectTree((this.selectedTree+1)%this.treeSelectorBtns.length);
  }
}

export function initializeScrollBooster(){
  let els = window.document.querySelectorAll(".scroll-booster");
  for (let i = 0; i < els.length; i++) {
    let content = els[i].querySelector(".scroll-booster-content");
    if (content) {
      new ScrollBooster({
        viewport: els[i],
        content: content,
        bounce: false,
        lockScrollOnDragDirection: "horizontal",
        direction: 'horizontal',
        scrollMode: "transform"});
    }
  }
}


let _animations = {};
export function initializeLottie(domElement, settings) {
  let lottieId = domElement.dataset.lottieId;
  if (lottieId !== undefined)
    _animations[lottieId].destroy();
  else lottieId = Object.keys(_animations).length;
  domElement.dataset.lottieId = lottieId;

  _animations[lottieId] = lottie.loadAnimation(Object.assign({
    container: domElement,
    path: domElement.dataset.lottie,
    renderer: 'svg',
    loop: true,
    autoplay: true,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice'
    }
  }, settings));
  return _animations[lottieId];
}

export function destroyLottie(domElement){
  let lottieId = domElement.dataset.lottieId;
  if (lottieId !== undefined)
    _animations[lottieId].destroy();
}

export function initializeLotties(settings){
  let lotties = window.document.querySelectorAll("*[data-lottie]");
  for (let i = 0; i < lotties.length; i++)
    initializeLottie(lotties[i], settings);
}